export const bodyScrollOverflowHidden = () => {
  document.querySelector('body').style.overflow = 'hidden';
};
export const bodyScrollOverflowAuto = () => {
  document.querySelector('body').style.overflow = 'auto';
};

//    
export const phoneCorrect = (phoneNumber) => {
  // phoneCorrect('380930534757') === +380 (93) 053 47 57
  let plus_group = phoneNumber.slice(0, 3);
  let code_group = phoneNumber.slice(3, 5);
  let first_group = phoneNumber.slice(5, 8);
  let second_group = phoneNumber.slice(8, 10);
  let third_group = phoneNumber.slice(10, 12);

  let correct_phone =
    '+' +
    plus_group +
    ' ' +
    '(' +
    code_group +
    ')' +
    ' ' +
    first_group +
    ' ' +
    second_group +
    ' ' +
    third_group;

  return correct_phone;
};
//    
export const phoneCorrectWithoutCode = (phoneNumber) => {
  // phoneCorrectWithoutCode('380930534757') === (93) 053 47 57
  let code_group = phoneNumber.slice(3, 5);
  let first_group = phoneNumber.slice(5, 8);
  let second_group = phoneNumber.slice(8, 10);
  let third_group = phoneNumber.slice(10, 12);

  let correct_phone =
    '(' + code_group + ')' + ' ' + first_group + ' ' + second_group + ' ' + third_group;

  return correct_phone;
};

//   
export const generateRandomString = (length = 15) => {
  let result = '';
  const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  const charactersLength = characters.length;
  let counter = 0;
  const time_milliseconds = new Date().getTime().toString();

  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));

    //    
    if (counter < time_milliseconds.toString().length) {
      result += time_milliseconds.charAt(counter);
    }
    counter += 1;
  }

  return result;
};

//    ::
export const getTimeMilliseconds = (milliseconds) => {
  // seconds
  let seconds = milliseconds / 1000;
  seconds = Math.round((seconds %= 60));
  // /seconds

  // minutes
  let minutes = Math.trunc(milliseconds / 60000);
  minutes = Math.trunc((minutes %= 60));
  // /minutes

  let hours = Math.trunc(milliseconds / 3600000);

  if (seconds < 10) seconds = '0' + seconds;
  if (minutes < 10) minutes = '0' + minutes;

  return hours + ':' + minutes + ':' + seconds;
};

//   
export const isArraysEqual = (firstArray, secondArray) => {
  return firstArray.toString() === secondArray.toString();
};
//       
export const isArraysIncludes = (array, value) => {
  return array.includes(value);
};

//  searchArray      itemSearch       textSearch          
export const searchSimilarText = (searchArray, itemSearch, textSearch) => {
  const result = [];

  //      "search" (     )
  const textSearchReplace = textSearch.replace(/([.?*+^$[\]\\(){}|-])/g, '');
  try {
    searchArray.forEach((item) => {
      if (item[itemSearch].toLowerCase().search(textSearchReplace.toLowerCase().trim()) != -1) {
        result.push(item);
      }
    });
    return result;
  } catch (e) {
    alert('  !!!');
    return [];
  }
};
